<template>
  <div class="submission">
    <!-- <textarea class="col-12" :value="JSON.stringify(queryAsGraph)" rows="7"></textarea> -->
    <div class="float-right col-7">
      <div id="query-cytoscape" style="height: 14em; border: 1px solid black; margin: 1em">
      </div>
      <button class="col-12" @click="cyQuery.fit()">Fit</button>
    </div>
    <div class="col-5">
      <h1>Enter query here</h1>
      <textarea class="col-12 text-pre-wrap" id="query" v-model="query" rows="7"></textarea>
      <button class="col-12" id="submission" @click="submitQuery(query)">Submit</button>
    </div>

    <!-- <textarea class="col-12" :value="JSON.stringify(queryAsGraph)" rows="7"></textarea> -->
    <div style="clear: both"></div>

    <div class="float-right col-7">
      <div id="result-cytoscape" style="height: 14em; border: 1px solid black; margin: 1em">
      </div>
      <button class="col-12" @click="cyResult.fit()">Fit</button>
    </div>
    <div class="col-5">
      <h1>Result appears here</h1>
      <textarea class="col-12 text-pre-wrap" id="result" v-model="result" rows="7"></textarea>
    </div>
  </div>
</template>

<script>
const cytoscape = window.cytoscape;

export default {
  name: 'Submission',
  props: {
    queryProp: {
      type: String,
      default: `{
        "nodes": [
          {
            "id": "n0",
            "type": "gene",
            "curie": "NCBIGENE:558"
          },
          {
            "id": "n1",
            "type": "biological_process"
          }
        ],
        "edges": [
          {
            "id": "e0",
            "source_id": "n1",
            "target_id": "n0",
            "type": "has_participant"
          }
        ]}`
    },
    resultProp: {
      type: String,
      default: `{
        "nodes": [
            {
                    "id": "obo:BFO_0000002"

            },
            {
                    "id": "GO:0010467"

            },
            {
                    "id": "NCBIGENE:558"

            },
            {
                    "id": "GO:0016310"

            },
            {
                    "id": "GO:0032259"

            }
        ],
        "edges": [
            {
                    "id": "e0000",
                    "source": "GO:0010467",
                    "target": "obo:BFO_0000002"

            },
            {
                    "id": "e0001",
                    "source": "GO:0010467",
                    "target": "NCBIGENE:558"

            },
            {
                    "id": "e0002",
                    "source": "GO:0016310",
                    "target": "obo:BFO_0000002"

            },
            {
                    "id": "e0003",
                    "source": "GO:0016310",
                    "target": "NCBIGENE:558"

            },
            {
                    "id": "e0004",
                    "source": "GO:0032259",
                    "target": "obo:BFO_0000002"

            },
            {
                    "id": "e0005",
                    "source": "GO:0032259",
                    "target": "NCBIGENE:558"

            }
        ]
    }`
    }
  },
  data: () => { return {
    query: "",
    result: "",
    style: [
      {
          "selector": "node",
          "style": {
              "label": "data(label)",
              "background-color": "#60f",
              "shape": "round-rectangle"
          }
      },
      {
          "selector": "edge",
          "style": {
              "content": "data(label)",
              "curve-style": "unbundled-bezier",
              "control-point-distances": [
                  -20,
                  20
              ],
              "control-point-weights": [
                  0.5
              ],
              "line-color": "#808080",
              "target-arrow-color": "#808080",
              "target-arrow-shape": "triangle",
              "target-arrow-fill": "filled"
          }
      }],
    };
  },
  mounted() {
    this.query = this.queryProp;
    this.result = this.resultProp;
  },
  computed: {
    cyQuery() { return cytoscape({
      container: document.getElementById('query-cytoscape'),
      style: this.style,
    }) },
    cyResult() { return cytoscape({
      container: document.getElementById('result-cytoscape'),
      style: this.style,
    }) },
    queryAsJSON() {
      try {
        return JSON.parse(this.query);
      } catch(err) {
        return {};
      }
    },
    queryAsGraph() {
      return {
        nodes: (this.queryAsJSON["nodes"] || []).map(node => {
          return {
            id: node.id,
            label: node.curie || node.type || node.id,
          };
        }),
        edges: (this.queryAsJSON["edges"] || []).map(edge => {
          return {
            label: edge.type || edge.id,
            id: edge.id,
            source: edge.source_id,
            target: edge.target_id,
          };
        })
      };
    },
    resultAsJSON() {
      try {
        return JSON.parse(this.result);
      } catch(err) {
        alert("Could not parse result: " + err);
        return {};
      }
    },
    resultAsGraph() {
      return {
        nodes: (this.resultAsJSON["nodes"] || []).map(node => {
          return {
            id: node.id,
            label: node.curie || node.type || node.id,
          };
        }),
        edges: (this.resultAsJSON["edges"] || []).map(edge => {
          return {
            label: edge.type || edge.id,
            id: edge.id,
            source: edge.source,
            target: edge.target,
          };
        })
      };
    }
  },
  watch: {
    query() {
      console.log("Setting ", this.cyQuery, " with nodes: ", this.queryAsGraph.nodes, " and edges:", this.queryAsGraph.edges);

      // When query is updated, redraw the query graph.
      this.cyQuery.remove('node');
      this.cyQuery.remove('edge');

      this.queryAsGraph.nodes.forEach(node => this.cyQuery.add({ group: 'nodes', data: node }));
      this.queryAsGraph.edges.forEach(edge => this.cyQuery.add({ group: 'edges', data: edge }));

      let layout = this.cyQuery.layout({ name: 'breadthfirst' });
      layout.run();

      this.cyQuery.fit();
    },
    result() {
      console.log("Setting ", this.cyResult, " from ", this.result, " with nodes: ", this.resultAsGraph.nodes, " and edges:", this.resultAsGraph.edges);

      // When result is updated, redraw the result graph.
      this.cyResult.remove('node');
      this.cyResult.remove('edge');

      this.resultAsGraph.nodes.forEach(node => this.cyResult.add({ group: 'nodes', data: node }));
      this.resultAsGraph.edges.forEach(edge => this.cyResult.add({ group: 'edges', data: edge }));

      let layout = this.cyResult.layout({ name: 'breadthfirst' });
      layout.run();

      this.cyResult.fit();
    },
  },
  methods: {
    submitQuery(query) {
      let parsed = null;

      try {
        parsed = JSON.parse(this.query);
      } catch(err) {
        alert("Could not parse JSON: " + err);
      }

      if (parsed == null) return;

      console.log("Submitting query: ", query);
      this.$http.post("http://robokop.renci.org:6434/query", {
        json: JSON.stringify({ message: { query_graph: this.queryAsJSON } }),
      }).then(result => {
        this.result = result;
      })
    }
  }
}
</script>
